$(".settings__btn").click(function () {
    let tab = $(this).attr("data-tab")
    $(".settings__btn").removeClass("_active")
    $(".settings__view").removeClass("_active")
    $(this).addClass("_active")
    $(`.settings__view[data-tab="${tab}"]`).addClass("_active")
    setActiveSettingsTab()

    if(window.innerWidth <= 992){
        $(".settings").addClass("_open-detail")
        $(".settings__buttons").fadeOut()
        $(".settings__view._active").fadeIn()
    }
})

setActiveSettingsTab()

function setActiveSettingsTab() {
    let activeTab = $(".settings__btn._active span").text()
    $(".settings__title-detail").html(activeTab)
}
